<template>
  <view class="user-center">
    <!-- 用户卡片 -->
    <user-card 
      :userInfo="userInfo" 
      @avatar-click="previewAvatar"
      @edit-click="navigateToEdit"
      @stat-click="handleStatClick"
    />
    
    <!-- 其他内容 -->
    <view class="other-content">
      <!-- 这里可以放置其他个人中心内容 -->
    </view>
  </view>
</template>

<script>
import userCard from '@/components/my/user-card.vue'

export default {
  components: { userCard },
  data() {
    return {
      userInfo: {
        avatar: '/static/avatars/user1.jpg',
        username: '创意达人',
        motto: '生活需要创意，代码需要艺术',
        isVip: true,
        badges: [
          { icon: '/static/badges/creative.png', name: '创意之星' },
          { icon: '/static/badges/active.png', name: '活跃用户' },
          { icon: '/static/badges/newbie.png', name: '新秀奖' }
        ]
      }
    }
  },
  methods: {
    previewAvatar() {
      uni.previewImage({
        urls: [this.userInfo.avatar]
      })
    },
    navigateToEdit() {
      uni.navigateTo({
        url: '/pages/user/edit'
      })
    },
    handleStatClick(index) {
      const titles = ['我的关注', '我的粉丝', '我的获赞', '我的收藏']
      uni.showToast({
        title: `点击了${titles[index]}`,
        icon: 'none'
      })
    }
  }
}
</script>

